import { Component, OnInit } from '@angular/core';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { LoadingService } from 'src/app/providers/Utilidades/LoadingService.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';
import { NavController, MenuController } from '@ionic/angular';
import { ApiSeguimientoService } from 'src/app/providers/Api/Seguimiento.service';
import { CargaElementosUsuariosService } from 'src/app/providers/cargas/usuarios.service';
import { Router } from '@angular/router';
import { ConexionService } from 'src/app/providers/Conexiones/Conexion.service';
import { trigger, transition, state, style, animate } from '@angular/animations';
import { UsuariosLoginService } from '../../providers/Api/UsuariosLogin.service';
import { RecordatoriosService } from '../../providers/Api/Recordatorios.service';
import { StatusBar } from '@ionic-native/status-bar/ngx';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.page.html',
  styleUrls: ['./menu.page.scss'],
  animations: [
    trigger('alerta', [
      state('open', style({
        background: '#FFEBAD',
        
      })),
      state('close', style({
        background: '#FFFFFF'
      })),
      transition('* => *', [
        animate('300ms')
      ])
    ])
  ]
})
export class MenuPage implements OnInit {

   public cantidadSeguimiento = 0;
   public notificaciones = 0;
   public recordatorios = 0;

   public red: string = 'wifi';
   public role = '';

    constructor(
        private almacenamiento: InicioService,
        private loading: LoadingService,
        private toastService: ToastServices,
        private navCtrl: NavController,
        private apiSeguimiento: ApiSeguimientoService,
        private cargaElementos: CargaElementosUsuariosService,
        private router: Router,
        private menuCtrl: MenuController,
        private conexion: ConexionService,
        private usuarioApi: UsuariosLoginService,
        private apiRecordatorios: RecordatoriosService,
        private statusbar: StatusBar
    ) {

     

    }

    ngOnInit() {

    }

    ionViewWillEnter() {
      this.statusbar.backgroundColorByHexString('#777777');
      this.capturarRole();
    }

    async capturarRole() {
      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
        this.role = info.role;

        this.apiSeguimiento.solicitudesRecibidas(info.token).subscribe((data: any) => {
          if (data.exe) {
            this.cantidadSeguimiento = data.response.SeguimientoData.length;
          }
        });

        this.usuarioApi.notiCantidad(info.token).subscribe((data: any) => {
          console.log(data);
          if (data.exe) {
            this.notificaciones = data.response;
            } 
        });

        this.apiRecordatorios.getRecordatorioPersona(info.token).subscribe((data: any) => {
            this.recordatorios = data.response.length;
        })
      }
    }

    async terminar() {
        try {
          const estado = await this.almacenamiento.terminarPartida();

          if (estado) {
            console.log(estado, 'ESTADO');
            this.loading.cargaProcesos('Cerrando sesión, te esperamos pronto').then( async () => {
              const abandonarPartida = await this.conexion.destruir(); 

              if (abandonarPartida) {
                console.log(abandonarPartida)
                console.log('Abandonar partida');
                this.cargaElementos.cancelarSuscripciones();
                this.router.navigate(['/home']);
                
                this.loading.finalizarCargado();
              } else {
                console.log('Abandonar partida falló');
                this.loading.finalizarCargado();
              }
             
            });

          } else {
            console.log('MUY MAL');
            this.router.navigate(['/home']);
          }

        } catch (error) {
          this.router.navigate(['/home']);
          this.loading.finalizarCargado();
          await this.toastService.alertApplication('No se pudo cerrar la sesión', false);
        }

      }

      // ir() {
      //   this.menuCtrl.close('perfilMenu').then(() => {
      //     this.router.navigate(['/central/perfil/solicitudes']);
      //   })
      // }

      // citas() {
      //   this.menuCtrl.close('perfilMenu').then(() => {
      //     this.router.navigate(['/citas']);
      //   })
      // }


}
